@import url('https://fonts.googleapis.com/css2?family=Monoton&family=Sofia&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.Loading {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #003049;
    font-family: sans-serif;
}

.loader {
    animation-name: animate;
    /*animation-fill-mode: ease-in-out;*/
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-duration: 4s;
    border: 1px solid rgb(3, 255, 234);
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 4px rgba(155, 255, 247, 0.4),
    0 0 6px rgba(3, 255, 234, 0.4),
    0 0 8px rgba(3, 255, 234, 0.4),
    0 0 10px rgba(3, 255, 234, 0.4);
}

.out-loader {
    width: 160px;
    height: 160px;
}

.inner-loader {
    width: 140px;
    height: 140px;
}

.inner-loader2 {
    width: 120px;
    height: 120px;
}

.inner-loader3 {
    width: 100px;
    height: 100px;
}

.inner-loader4 {
    width: 50px;
    height: 50px;
    background-color: rgb(3, 255, 234);
    border-radius: 50%;
    box-shadow: 0 0 4px rgba(155, 255, 247, 0.4),
    0 0 6px rgba(3, 255, 234, 0.4),
    0 0 8px rgba(3, 255, 234, 0.4),
    0 0 10px rgba(3, 255, 234, 0.4);
}

.p {
    font-family: sofia;
    font-size: 80px;
    text-transform: uppercase;
    margin-top: 40px;
    position: relative;
    color: rgb(0, 232, 248);
    letter-spacing: 2px;
}
@keyframes animate {
    0%{
        transform: rotateZ(90deg);
    }
    16.6%{
        transform: rotateZ(180deg);
    }
    33.2%{
        transform: rotateZ(270deg);
    }
    49.8%{
        transform: rotateZ(360deg);
    }
    66.4%{
        transform: rotateZ(270deg);
    }
    83%{
        transform: rotateZ(180deg);
    }
    100%{
        transform: rotateZ(90deg);
    }
}